'use client';

import { useContext, useMemo, useState } from 'react';
import { AppContext, AppContextType } from '@/contexts/app-context';
import { DateRangePicker } from '@/components/ui/date-range-picker';
import { DashboardSummary } from './dashboard-summary';
import { RevenueChart } from './revenue-chart';
import { startOfMonth, endOfMonth } from 'date-fns';

export function Dashboard() {
  const {
    allServices,
    expenses,
    t,
  } = useContext(AppContext) as AppContextType;

  const [dateRange, setDateRange] = useState<{ from: Date; to: Date }>(() => {
    const today = new Date();
    return { from: startOfMonth(today), to: endOfMonth(today) };
  });

  const filteredData = useMemo(() => {
    const { from, to } = dateRange;
    const services = allServices.filter(s => new Date(s.timestamp) >= from && new Date(s.timestamp) <= to);
    const filteredExpenses = expenses.filter(e => new Date(e.date) >= from && new Date(e.date) <= to);
    return { services, expenses: filteredExpenses };
  }, [allServices, expenses, dateRange]);

  const totalRevenue = useMemo(() => filteredData.services.reduce((acc, s) => acc + s.price, 0), [filteredData.services]);
  const totalCommissions = useMemo(() => filteredData.services.reduce((acc, s) => acc + s.commission, 0), [filteredData.services]);
  const totalExpenses = useMemo(() => filteredData.expenses.reduce((acc, e) => acc + e.amount, 0), [filteredData.expenses]);
  const netProfit = useMemo(() => totalRevenue - totalCommissions - totalExpenses, [totalRevenue, totalCommissions, totalExpenses]);

  return (
    <div className="container mx-auto p-4">
      <div className="flex justify-between items-center mb-4">
        <h1 className="text-2xl font-bold">{t('dashboard')}</h1>
        <DateRangePicker 
          onUpdate={({ range }) => setDateRange({ from: range.from, to: range.to || range.from })}
          initialDateFrom={dateRange.from}
          initialDateTo={dateRange.to}
          align="end"
          locale="en-GB"
          showCompare={false}
        />
      </div>
      <DashboardSummary 
        totalRevenue={totalRevenue}
        totalCommissions={totalCommissions}
        totalExpenses={totalExpenses}
        netProfit={netProfit}
      />
      <div className="mt-8">
        <RevenueChart services={filteredData.services} expenses={filteredData.expenses} />
      </div>
    </div>
  );
}
